const merge = require('lodash/merge')
const isMatch = require('lodash/isMatch')
const uniq = require('lodash/uniq')

const behaviorSchemas = require('./data/extracted-data/behavior-schemas.json')
const behaviorBindings = require('./data/extracted-data/behaviors.json')
const behaviorSchemaOverlays = require('./data/overlay-data/behavior-schemas-overlay.json')
const behaviorOverlays = require('./data/overlay-data/behaviors-overlay.json')

function mergeBehaviorOverlays (base, overlays) {
  const baseValues = Array.isArray(base) ? base : Object.values(base)

  for (const value of baseValues) {
    for (const { match, ...overlay } of overlays) {
      if (isMatch(value, match)) {
        merge(value, overlay)
      }
    }
  }
}

mergeBehaviorOverlays(behaviorSchemas, behaviorSchemaOverlays)
mergeBehaviorOverlays(behaviorBindings, behaviorOverlays)

const standardBehaviorIncludeFiles = uniq(
  behaviorOverlays.flatMap(overlay => overlay.includes || [])
)

for (const behavior of behaviorBindings) {
  const schema = behaviorSchemas[behavior.compatible]
  merge(behavior, { schema })
}

module.exports = {
  behaviorSchemas,
  behaviorBindings,
  behaviorSchemaOverlays,
  standardBehaviorIncludeFiles
}
